import React from "react";
import { Card, Table, Modal, Button, message } from "antd";
import axios from "./../../axios/axios";
import Utils from '../../util/util'

export default class BasicTable extends React.Component {
  state = {
    dataSource2: []
  };
  param={
      page:1
  }

  componentDidMount() {
    const data = [
      {
        id: "0",
        userName: "Jack",
        sex: "1",
        state: "1",
        interest: "1",
        birthday: "2000-01-01",
        address: "北京市海淀区奥林匹克公园",
        time: "09:00"
      },
      {
        id: "1",
        userName: "Tom",
        sex: "1",
        state: "1",
        interest: "1",
        birthday: "2000-01-01",
        address: "北京市海淀区奥林匹克公园",
        time: "09:00"
      },
      {
        id: "2",
        userName: "Lily",
        sex: "1",
        state: "1",
        interest: "1",
        birthday: "2000-01-01",
        address: "北京市海淀区奥林匹克公园",
        time: "09:00"
      }
    ];
    data.map((item,index)=>{
        item.key = index;
    })
    this.setState({
      dataSource: data
    });
    
    this.request();
  }
  handleDelete=()=>{
      let rows=this.state.selectedRows;
      let ids=[]
      rows.map((item)=>{
          ids.push(item.id)
      })
      Modal.confirm({
        title:'删除提示',
        content: `您确定要删除这些数据吗？${ids.join(',')}`,
      })
  }
  //   获取mock数据
  request() {
    axios
      .ajax({
        url: "/table/list",
        data: {
          isShowLoading: true,
          page:this.param.page
        }
      })
      .then(res => {
        res.result.list.map((item, index) => {
            item.key = item.id;
        })
        this.setState({
          dataSource2: res.result.list,
          selectedRowKeys:[],
          selectedRows:null,
          pagination:Utils.pagination(res,(current)=>{
              console.log(current)
              this.param.page=current;
              this.request()
          })
        });
      });
  }
  onSelectChange=(selectedRowKeys,selectedRows)=>{
    console.log('selectedRowKeys changed: ', selectedRows);
    this.setState({ selectedRowKeys,selectedRows });
  }
  render() {
    const columns = [
      {
        title: "id",
        key: "id",
        dataIndex: "id"
      },
      {
        title: "用户名",
        key: "userName",
        dataIndex: "userName"
      },
      {
        title: "性别",
        key: "sex",
        dataIndex: "sex",
        render(sex) {
          return sex == 1 ? "男" : "女";
        }
      },
      {
        title: "状态",
        key: "state",
        dataIndex: "state",
        render(state) {
          let config = {
            "1": "咸鱼一条",
            "2": "风华浪子",
            "3": "北大才子",
            "4": "百度FE",
            "5": "创业者"
          };
          return config[state];
        }
      },
      {
        title: "爱好",
        key: "interest",
        dataIndex: "interest",
        render(abc) {
          let config = {
            "1": "游泳",
            "2": "打篮球",
            "3": "踢足球",
            "4": "跑步",
            "5": "爬山",
            "6": "骑行",
            "7": "桌球",
            "8": "麦霸"
          };
          return config[abc];
        }
      },
      {
        title: "生日",
        key: "birthday",
        dataIndex: "birthday"
      },
      {
        title: "地址",
        key: "address",
        dataIndex: "address"
      },
      {
        title: "早起时间",
        key: "time",
        dataIndex: "time"
      }
    ];
    const selectedRowKeys = this.state.selectedRowKeys;
    const rowSelection={
        selectedRowKeys,
        onChange:this.onSelectChange
    }
    return (
      <div>
        <Card title="基础表格">
          <Table
            bordered
            columns={columns}
            dataSource={this.state.dataSource}
            pagination={false}
          />
        </Card>
        <Card title="动态获取数据表格">
        <Table
            bordered
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={false}
          />
        </Card>
        <Card title="多选框表格">
        <Button type="primary" style={{marginBottom:10}} onClick={this.handleDelete}>删除</Button>
        <Table
            rowSelection={rowSelection}
            bordered
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={false}
          />
        </Card>
        <Card title="分页表格">
        <Button type="primary" style={{marginBottom:10}} onClick={this.handleDelete}>删除</Button>
        <Table
            rowSelection={rowSelection}
            bordered
            columns={columns}
            dataSource={this.state.dataSource2}
            pagination={this.state.pagination}
          />
        </Card>
      </div>
    );
  }
}
